import service from '@/http/services/publicClass.js'
export default {
  name: "PayCenter",
  created(){
    this.countDown(1024);
  },
  data(){
    return{
      courseid: '',
      countStr: "00:00:00",
      payModal: false,
      course:'',
    }
  },
  methods:{
    goPay(){
      this.payModal = true;
    },
    hideModal(){
      this.payModal = false;
    },
    shoeCourse() {
      service.publicClassService.courseDetail({'course_id':this.courseid}).then(res => {
        if (res.status === 200) {
          console.log(res.data);
          this.course=res.data;
        }
      })
    }
  },
  mounted() {
    this.courseid=this.$route.params.id;
    this.shoeCourse();
  }
}



