
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import  service from '@/http/services/package.js'
export default {
name: "SetMealDetail",
data(){
return{
pid: 0,
datas:['1','2'],
activeIndex: 0,
share: false,
packageDetail: {
package: '',
course: ''
},
pay: false,
collect: false,
}
},
created() {
this.pid = this.$route.params.pid;
console.log("pid",this.$route.params.pid);
},
components: {
Tab,
TabItem
},
methods:{
onItemClick(index){
this.activeIndex = index;
},
changeShare(msg){
this.share = msg;
},
showShare(){
this.share = true;
},

getPackageDetail() {
service.packageService.packageDetail({pid: this.pid}).then(res => {
if (res.status === 200 && res.data.status === 0) {
this.packageDetail.package = res.data.package;
this.packageDetail.course = res.data.course;
console.log(this.packageDetail);
} else {
alert('wrong!');
}
})
}
},

mounted: function () {
this.getPackageDetail();
}
}



